#greeting {
    width: 100vw;
    height: 100vh;
    display: grid;
    position: fixed;
    place-items: center;
    grid-template-rows: minmax(auto, 35%) auto minmax(10%, auto);
    --background-minion: url(/portfolio/static/media/minion-background.846b16e9.jpg);
    --background-banana: url(/portfolio/static/media/repeat-banana.c3d3868d.png);
}

#greeting_title {
    margin: 0;
    text-align: center;
    z-index: 1;
}

.greeting_circle {
    border-radius: 50%;
    width: 35%;
    min-width: 280px;
    display: -webkit-inline-flex;
    display: inline-flex;
    place-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.greeting_circle::before {
    content: "";
    display: block;
    padding-bottom: 100%;
}

#outer_circle {
    -webkit-animation-name: spin2;
            animation-name: spin2;
    -webkit-animation-duration: 20s;
            animation-duration: 20s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

#inner_circle {
    -webkit-animation-name: spin;
            animation-name: spin;
    -webkit-animation-duration: 20s;
            animation-duration: 20s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    width: 60%;
    min-width: 0px;
    background-color: rgba(175, 255, 251, 0.384);
    background-image: url(/portfolio/static/media/banana4.f00fe9ef.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    border: 3px solid rgb(182, 224, 216);
    cursor: pointer;
    place-items: center;
}

#inner_circle::after {
    content: "";
    background: linear-gradient(135deg, hsl(180, 51%, 85%), hsl(180, 45%, 78%), hsl(236, 59%, 87%), hsl(295, 80%, 90%));
    border-radius: 50%;
    width: 0%;
    padding-bottom: 0%;
    transition: 1s;
}

#inner_circle:hover:after {
    width: 100%;
    padding-bottom: 100%;
}

#inner_circle p {
    text-align: center;
    font-size: 3rem;
    position: fixed;
    visibility: hidden;
    opacity: 0;
}

#inner_circle:hover p {
    visibility: visible;
    opacity: 1;
    transition: 1s;
}

