.glitch {
    color: #c392ce;
    font-size: 10vh;
    text-transform: uppercase;
    position: relative;
    display: inline-block;
    font-family: "Archivo Black", sans-serif;
    text-align: center;
}

.glitch::before,
.glitch::after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.glitch::before {
    left: 2px;
    text-shadow: -2px 0 #dbfecc;
    clip: rect(24px, 550px, 90px, 0);
    animation: glitch_anim-2 3s infinite linear alternate-reverse;
}

.glitch::after {
    left: -2px;
    text-shadow: -2px 0 #efccfe;
    clip: rect(85px, 550px, 140px, 0);
    animation: glitch_anim 2.5s infinite linear alternate-reverse;
}
